<template>
  <div class="detail-box">
    <div class="title-box">
      <i class="el-icon-goods"></i>
      硬货
    </div>
    <div class="content-box">
      <img :src="this.message.imgSrc" />
      <div class="left-desc">
        <div class="name">{{this.message.desc}}</div>
        <div class="price">{{this.message.price}}</div>
      </div>
      <div class="bottom-desc">
        <div class="judge">描述：{{this.message.judge}}</div>
        <div class="level">评级：{{this.message.level}}</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      message: null
    };
  },
  created() {
    const id = this.$route.params.id;
    const shopList = this.$store.state.shopList[0];
    for (let index = 0; index < shopList.length; index++) {
      if (shopList[index].id === id) {
        this.message = shopList[index];
      }
    }
    console.log(this.message);
  }
};
</script>
<style lang="less" scoped>
@import "../../assets/css/detail.less";
</style>